import {
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Typography,
} from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { RouteComponentProps } from 'react-router-dom';

import './store-feature-product-detail.module.scss';
import { formatRating } from '@nxegghead/store/util-formatters';
import { Product } from '@nxegghead/api/util-interfaces';

type TParams = { id: string };

/* eslint-disable-next-line */
export interface StoreFeatureProductDetailProps
  extends RouteComponentProps<TParams> {}

export const StoreFeatureProductDetail = (
  props: StoreFeatureProductDetailProps
) => {
  const [state, setState] = useState<{
    data: Product;
    loadingState: 'success' | 'error' | 'loading';
  }>({
    data: null,
    loadingState: 'success',
  });

  useEffect(() => {
    setState({
      ...state,
      loadingState: 'loading',
    });
    const productId = props.match.params.id;
    fetch(`/api/products/${productId}`)
      .then((x) => x.json())
      .then((res) => {
        setState({
          ...state,
          data: res,
          loadingState: 'success',
        });
      })
      .catch((err) => {
        setState({
          ...state,
          loadingState: 'error',
        });
      });
  }, [props.match.params.id]);

  const myStyle = {
    height: '140px',
  };

  return (
    <div className="container">
      {state.loadingState === 'loading' ? (
        'Loading…'
      ) : state.loadingState === 'error' ? (
        '<div>Error retrieving data</div>'
      ) : state.data == null ? (
        ''
      ) : (
        <Card>
          {/* <CardMedia
            className="product-card-media"
            image={state.data.image}
            title={state.data.name}
          /> */}
          <img src={state.data.image} alt={state.data.name} style={myStyle} />
          <CardActionArea>
            <CardContent>
              <Typography variant="body2" color="textSecondary" component="p">
                {/* {props.match.params.id}  */}
                {state.data.name}
              </Typography>

              <Typography
                variant="body2"
                color="textSecondary"
                component="p"
                className="product-ranking"
              >
                <strong>Rating:</strong> {formatRating(state.data.rating)}
              </Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      )}
    </div>
  );
};

export default StoreFeatureProductDetail;
